@mixin textfield() {
  @include appearance( none );
  background: #fcfcfc;
  background-clip: padding-box;
  border-radius: 3px;
  border: 1px solid rgba(#000, 0.15);
  box-shadow: inset 0 1px 2px rgba(#000, 0.1);
  color: $dark-steel;
  outline: none;
  padding: 0 12px;
  &:focus { background: #fff; }
}
